.ng2-c-toggle {
  background-color: #cccccc;
  border-radius: 7px;
  cursor: pointer;
  display: inline-block;
  height: 14px;
  margin: 10px 5px;
  position: relative;
  transition: background-color 300ms;
  user-select: none;
  vertical-align: middle;
  width: 34px; }
  .ng2-c-toggle::after {
    bottom: -10px;
    content: '';
    left: -10px;
    position: absolute;
    right: -10px;
    top: -10px; }
  .ng2-c-toggle::before {
    background-image: linear-gradient(#ffffff, #f5f5f5);
    border-radius: 50%;
    box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.12), 0 2px 2px 0 rgba(0, 0, 0, 0.25);
    content: '';
    height: 20px;
    left: 0;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    transition: transform 300ms;
    width: 20px; }

.ng2-c-toggle__loader {
  left: -3px;
  position: absolute;
  top: -6px;
  transition: transform 300ms; }

.ng2-c-toggle--checked {
  background-color: #6ec60f; }
  .ng2-c-toggle--checked::before {
    transform: translate(14px, -50%); }
  .ng2-c-toggle--checked .ng2-c-toggle__loader {
    transform: translateX(14px); }
